/* You can add global styles to this file, and also import other style files */

body {
	background: #041d35;
	color: white;
   margin: 0;
   overflow-x: hidden;
}

@font-face {
	font-family: Ubuntu;
	src: url(assets/fonts/Ubuntu-Bold.ttf);
}

@font-face {
	font-family: Raleway;
	src: url(assets/fonts/Raleway-SemiBold.ttf);
}

@font-face {
	font-family: "Source Sans Pro";
	src: url(assets/fonts/SourceSansPro-Black.ttf);
}

@font-face {
	font-family: "Noto Sans";
	src: url(assets/fonts/NotoSans-Regular.ttf);
}

.clocking {
   color: transparent;
   &::after {
       content: "";
       position: absolute;
       left: 0;
       right: 0;
       top: 0;
       bottom: 0;
       margin: auto;
       border-radius: 100%;
       border: 5px solid transparent;
       border-top: 5px solid white;
       animation: 0.5s rotate linear infinite;
       height: 10px;
       width: 10px;
       left: 0;
       top: 0;
   }
}


@keyframes rotate {
   from {
       transform: rotate(0deg);
   }
   to {
       transform: rotate(360deg);
   }
}

@keyframes slideout {
   from {
       left: 0;
   }
   to {
       left: -200vw;
   }
}

@keyframes slidein {
   from {
       left: 200vw;
   }
   to {
       left: 0;
   }
}

@keyframes invalid {
   0% {
       background: transparent;
   }
   20% {
       background: red;
   }
   100% {
       background: transparent;
   }
}